@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
* {
  box-sizing: border-box;
}
body {
  all: unset;
  overflow-x: hidden;
}

canvas {
  height: 100vh;
  width: 100vh;
}

.canvas-board {
  height: 100%;
  width: 100%;
  position: absolute;
  z-index: -100;
}
.hello-world {
  position: absolute;
  top: 15%;
  left: 7%;
  font-family: "Oswald", sans-serif;
  font-size: 7rem;
  opacity: 0.3;
}
.quote {
  position: absolute;
  top: 45%;
  left: 13%;
  font-family: "oswald", sans-serif;
  opacity: 0.3;
  font-size: 4rem;
}

.navbar {
  padding: 4px 10px;
  margin: 0 5px;
  position: absolute;
  top: 2%;
  display: flex;
  width: 99%;
  justify-content: space-around;
  border-radius: 5px;
}
.navbar-brand {
  text-decoration: none;
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
  font-weight: bold;
  color: black;
  transition-duration: 500ms;
}
.navbar-brand:hover {
  transform: rotateY(180deg);
}
.nav-links {
  display: flex;
  gap: 14px;
}

.nav-link {
  margin-top: 12px;
  text-decoration: none;
  color: black;
  font-size: 1.2rem;
  transition-duration: 200ms;
}
.nav-link:hover {
  transform: translateY(-50%);
}

.experience {
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 10px;
  padding-right: 10px;
  display: grid;
  grid-template-areas: "one two five" "three four five";
  border: 1px solid;
  margin: 2rem 2rem;
}

.card:nth-of-type(1) {
  grid-area: one;
}

.card:nth-of-type(2) {
  grid-area: two;
}

.card:nth-of-type(3) {
  grid-area: four;
}

.card:nth-of-type(4) {
  grid-area: three;
}

.card:nth-of-type(5) {
  grid-area: five;
}

.card {
  margin: 5px 10px;
  padding: 2px 5px;
  border: 3px solid black;
  font-family: "Poppins", sans-serif;
}

.exp-name {
  font-family: "oswald";
  text-align: center;
  font-size: 1.75rem;
  font-weight: bolder;
}

.exp-desc {
  text-align: left;
  font-weight: 500;
  font-size: 1.25rem;
}
.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.exp-year {
  padding-top: 5px;
  font-weight: bold;
}
.skill {
  margin: 3px 5px;
  padding: 2px 10px;
  border: 2px solid black;
  border-radius: 8px;
  background-color: black;
  color: white;
}
.ed-description {
  margin-top: 0px;
  font-size: small;
  font-weight: 400;
  margin-bottom: 0px;
}
.bullets {
  margin: 0px;
}
.achievements {
  text-align: left;
}

#projects {
  height: 100vh;
  position: relative;
}
.canvas-project {
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  position: absolute !important;
  z-index: -100;
  box-sizing: border-box;
}
.d-grid {
  height: 100%;

  position: relative;
  display: grid;
  grid-template-areas: "one two three" "four five six";
  justify-content: space-evenly;
  place-items: center;
  gap: 40px;
}
.p-card {
  font-family: "Poppins", sans-serif;
  width: 400px;
  height: 250px;
  border: 3px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
}
.p-card:nth-of-type(1) {
  grid-area: one;
}

.p-card:nth-of-type(2) {
  grid-area: two;
}

.p-card:nth-of-type(3) {
  grid-area: three;
}

.p-card:nth-of-type(4) {
  grid-area: four;
}

.p-card:nth-of-type(5) {
  grid-area: five;
}

.p-card:nth-of-type(6) {
  grid-area: six;
}
.p-card:hover {
  -webkit-box-shadow: 8px 8px 25px -3px rgba(0, 0, 0, 0.51);
  -moz-box-shadow: 8px 8px 25px -3px rgba(0, 0, 0, 0.51);
  box-shadow: 8px 8px 25px -3px rgba(0, 0, 0, 0.51);
  border: 1.5px solid black;
}
.abt-heading {
  font-family: "Poppins", sans-serif;
  font-weight: bolder;
}
.desc-p {
  margin: 0;
}
.contact-us-page {
  height: 100%;
  text-align: center;
  justify-content: center;

  margin: 1rem 2rem;
}
.contact {
  font-family: "Poppins", sans-serif;
  font-weight: 200px;
  font-size: 50px;
  opacity: 0.5;
  transform: rotate(-90deg);
}
.contact-details {
  height: 40%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items:center;
}
.mail {
  all: unset;
  font-family: "Poppins", sans-serif;
  font-weight: 200px;
  font-size: 2.2rem;
  width: 350px;
  border: 2px solid black;
  cursor: pointer;
  margin-bottom: 9px;
}
.linked-in {
  all: unset;
  font-family: "Poppins", sans-serif;
  font-weight: 200px;
  font-size: 2.2rem;
  width: 350px;
  border: 2px solid black;
  cursor: pointer;
  margin-bottom: 9px;
  color: #0A66C2;
}
.logo-contact{
  height: 2rem;
  margin-left: 0;
  padding-top:2px ;
}
.call{
  all: unset;
  font-family: "Poppins", sans-serif;
  font-weight: 200px;
  font-size: 2.2rem;
  width: 350px;
  border: 2px solid black;
  cursor: pointer;
  margin-bottom: 9px;
}
.contact-us-page {
  margin-top: 35px;
  transform: translate(87%, 80%);
  transition: all 1s ease-out;
  transition: 600ms 1s;
}
.contact-us-page:hover {
  
  transform: translate(-10%, 80%);
  transition: all 1s ease-out;
}